.app {
  font-family: 'Poppins', sans-serif;
  line-height: 1.5;
  color: var(--clr-fg);
  background-color: var(--clr-bg);
}
.light {
  --clr-bg: #fcfcfc;
  --clr-bg-alt: #fff;
  --clr-fg: #555;
  --clr-fg-alt: #444;
  --clr-primary: #2978b5;
  --shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}
.dark {
  --clr-bg: #23283e;
  --clr-bg-alt: #2a2f4c;
  --clr-fg: #bdbddd;
  --clr-fg-alt: #cdcdff;
  --clr-primary: #90a0d9;
  --shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px,
    rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}
main {
  max-width: 1100px;
  width: 95%;
  margin: 0 auto;
}
.section {
  margin-top: 5em;
}
.section-title {
  text-align: center;
  margin-bottom: 1em;
  text-transform: uppercase;
}
.center {
  display: flex;
  align-items: center;
}
.link {
  color: var(--clr-primary);
  padding: 0 0 0.3em 0;
  position: relative;
  &:hover {
    color: var(--clr-primary);
    &::before {
      width: 100%;
    }
  }
  &::before {
    content: '';
    display: inline;
    width: 0%;
    height: 0.2em;
    position: absolute;
    bottom: 0;
    background-color: var(--clr-primary);
    transition: width 0.2s ease-in;
  }
  &:focus {
    &::before {
      width: 100%;
    }
  }
}
.link-nav {
  color: var(--clr-fg);
  text-transform: lowercase;
  font-weight: 500;
}
.link-icon {
  color: var(--clr-fg);
}
.btn {
  display: block;
  cursor: pointer;
  padding: 0.8em 1.4em;
  font-weight: 500;
  font-size: 0.9rem;
  text-transform: lowercase;
  transition: transform 0.2s ease-in-out;
}
.btn-outline {
  color: var(--clr-primary);
  border: 2px solid var(--clr-primary);
  position: relative;
  overflow: hidden;
  z-index: 1;
  &:hover {
    color: var(--clr-bg);
    &:before {
      right: 0;
    }
  }
  &:focus {
    color: var(--clr-bg);
    &:before {
      right: 0;
    }
  }
  &:before {
    content: '';
    position: absolute;
    background-color: var(--clr-primary);
    right: 100%;
    bottom: 0;
    left: 0;
    top: 0;
    z-index: -1;
    transition: right 0.2s ease-in-out;
  }
}
.btn-plain {
  text-transform: initial;
  background-color: var(--clr-bg-alt);
  box-shadow: rgba(0, 0, 0, 0.15) 0px 3px 3px 0px;
  border: 0;
  &:hover {
    transform: translateY(-4px);
  }
}
.btn-icon {
  padding: 0;
  &:hover {
    color: var(--clr-primary);
  }
  &:focus {
    color: var(--clr-primary);
  }
  &:active {
    transform: translateY(-5px);
  }
}
@media (max-width: 600px) {
  .section {
    margin-top: 4em;
  }
}
