.nav-list {
  margin-right: 1.5em;
  display: flex;
}
.nav-list-item {
  margin-left: 1.5em;
}
.app {
  .nav-hamburger {
    display: none;
  }
}
.nav-theme {
  margin-top: 0.4em;
}
@media (max-width: 600px) {
  .nav-list {
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    inset: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
  }
  .nav-list-item {
    margin: 0.5em 0;
  }
  .app {
    .nav-hamburger {
      display: flex;
      z-index: 2;
      margin-left: 0.8em;
    }
  }
}
